import { Point } from './commonTypes';

export type DataTypes =
	| number
	| string
	| boolean
	| { x: number; y: number }
	| { x: number; y: number; z: number }
	| { width: number; height: number }
	| { x: number; y: number; width: number; height: number };

export type Ease = [number, number, number, number];

export enum KeyframeType {
	Static = 'static',
	Relative = 'relative',
}

/** only relative keyframes could have CurveType property */
export enum CurveType {
	Continous = 'continous',
	Free = 'free',
}

// start, startControl, endControl, end
export interface curveControls {
	start: Point;
	startControl: Point;
	endControl: Point;
	end: Point;
}

export interface Keyframe {
	name?: string;
	safeName?: string;
	type: KeyframeType;
	curveType?: CurveType;
	time: number;
	value: number; // DataTypes;
	ease: Ease | null;
}

export interface EdgeNumbers {
	min: number;
	max: number;
}

export interface DrawKeyframe {
	start: Point | null;
	end: Point | null;
	startControl: Point | null;
	endControl: Point | null;
	type: KeyframeType;
}

export enum KeyframeAspect {
	Start = 'start',
	End = 'end',
	StartControl = 'startControl',
	EndControl = 'endControl',
}

export interface ActivePoint {
	idx: number;
	aspect: KeyframeAspect;
}
