import './App.css';
import { useEffect } from 'react';
import CurveEditor from './curveeditor/CurveEditor';
import { easingEditorTheme, exampleKeyframes } from './curveeditor/utils';

function isMobile() {
	const toMatch = [/Android/i, /webOS/i, /iPhone/i, /iPad/i, /iPod/i, /BlackBerry/i, /Windows Phone/i];

	return toMatch.some(toMatchItem => {
		return navigator.userAgent.match(toMatchItem);
	});
}
const App = () => {
	useEffect(() => {
		// block Safari gestures
		if (isMobile()) {
			window.addEventListener('gesturestart', e => e.preventDefault());
			window.addEventListener('gesturechange', e => e.preventDefault());
			window.addEventListener('gestureend', e => e.preventDefault());
		}
	});
	const firstFrame = exampleKeyframes[0];
	const lastFrame = exampleKeyframes[exampleKeyframes.length - 1];
	return (
		<div className="App">
			<CurveEditor
				theme={easingEditorTheme}
				keyframes={exampleKeyframes}
				valueMap={{ min: firstFrame.value, max: lastFrame.value }}
				timeMap={{ min: firstFrame.time, max: lastFrame.time }}
				centered={true}
			></CurveEditor>
		</div>
	);
};

export default App;
