/* eslint-disable node/no-unsupported-features/es-syntax */
import { Point, Rect, Size } from '../commonTypes';

export interface ProjectionStatus {
	pan: boolean;
	zoom: boolean;
}

export class Projection {
	gap = 20;
	scaleEdges = {
		min: 0.2,
		max: 2,
	};
	minWidth = 0;
	maxWidth = 0;
	bounds: Rect = { x: 0, y: 0, width: 0, height: 0 };
	anchor: Point = { x: 0, y: 0 };
	eventOriginPoint: Point = { x: 0, y: 0 };
	shift: Point = { x: 0, y: 0 };
	scale = 1;
	sourceDimensions: Size = { width: 0, height: 0 };
	renderRectangle: Rect = { x: 0, y: 0, width: 0, height: 0 };
	originRectangle: Rect = { x: 0, y: 0, width: 0, height: 0 };
	displaySetup: Size = { width: 0, height: 0 };

	prevPos: Point = { x: 0, y: 0 };
	prevShift: Point = { x: 0, y: 0 };

	public updateDisplaySize(displaySize: Size) {
		this.displaySetup = { ...displaySize };
	}

	public resetBounds(bounds) {
		this.bounds = bounds;
	}

	public setScaleEdges(edges: { min: number; max: number }) {
		this.scaleEdges = { ...edges };
		this.updateEdgeValues();
	}

	public getScale() {
		return this.renderRectangle.width / this.originRectangle.width;
	}

	public startZoom(anchor, eventPoint) {
		this.registerState();
		this.anchor = anchor;
		this.eventOriginPoint = eventPoint;
	}

	public updateZoom(eventAnchor, eventPoint) {
		const scaleDistance = this.getDistance(eventAnchor, eventPoint) * 3 - this.getDistance(this.anchor, this.eventOriginPoint) * 3;
		let scale = (this.renderRectangle.width + scaleDistance) / this.renderRectangle.width;
		if (this.renderRectangle.width * scale > this.maxWidth) {
			scale = 1 - this.renderRectangle.width / this.maxWidth + 1;
		}
		if (this.renderRectangle.width * scale < this.minWidth) {
			scale = 1 - this.renderRectangle.width / this.minWidth + 1;
		}
		this.scale = scale;
		const anchor = {
			x: this.anchor.x - this.bounds.x,
			y: this.anchor.y - this.bounds.y,
		};

		const zoomShift = {
			x: this.renderRectangle.x - (anchor.x - (anchor.x - this.renderRectangle.x) * this.scale),
			y: this.renderRectangle.y - (anchor.y - (anchor.y - this.renderRectangle.y) * this.scale),
		};

		this.shift = {
			x: eventAnchor.x - this.anchor.x - zoomShift.x,
			y: eventAnchor.y - this.anchor.y - zoomShift.y,
		};
	}

	public stopZoom() {
		this.registerState();
		this.anchor = { x: 0, y: 0 };
		this.eventOriginPoint = { x: 0, y: 0 };
	}

	public startPan(anchor: Point) {
		this.registerState();
		this.anchor = anchor;
	}

	public updatePan(point: Point) {
		this.shift = {
			x: point.x - this.anchor.x,
			y: point.y - this.anchor.y,
		};
	}

	public stopPan() {
		this.registerState();
		this.anchor = { x: 0, y: 0 };
	}

	public getOriginRectangle() {
		return this.originRectangle;
	}

	public getRenderRectangle() {
		return this.getModifiedRectangle();
	}

	public setOriginRectangle(origin: Rect, reset) {
		this.originRectangle = { ...origin };
		this.updateEdgeValues();
		this.sourceDimensions = {
			width: origin.width,
			height: origin.height,
		};
		if (reset) {
			this.renderRectangle = {
				x: this.originRectangle.x,
				y: this.originRectangle.y,
				width: this.originRectangle.width,
				height: this.originRectangle.height,
			};
		}
	}

	public setRenderRectangle(renderRect: Rect) {
		if (this.scale || this.shift.x || this.shift.y) {
			this.registerState();
		}
		this.renderRectangle = { ...renderRect };
	}

	public isTouchPad = evt => (evt['wheelDeltaY'] ? evt['wheelDeltaY'] === -3 * evt.deltaY : evt.deltaMode === 0);

	private updateEdgeValues() {
		this.minWidth = this.scaleEdges.min * this.originRectangle.width;
		this.maxWidth = this.scaleEdges.max * this.originRectangle.width;
	}

	private registerState() {
		this.renderRectangle = this.getModifiedRectangle();
		this.shift = { x: 0, y: 0 };
		this.scale = 1;
	}

	private getModifiedRectangle() {
		if (!this.renderRectangle.width || !this.renderRectangle.height) {
			return this.originRectangle;
		}

		const rect = {
			x: this.renderRectangle.x + this.shift.x,
			y: this.renderRectangle.y + this.shift.y,
			width: this.renderRectangle.width * this.scale,
			height: this.renderRectangle.height * this.scale,
		};

		let lockPosition = 0;
		if (rect.width < this.minWidth) {
			lockPosition = -1;
			rect.width = this.minWidth;
			rect.height = (this.minWidth / this.originRectangle.width) * this.originRectangle.height;
		}
		if (rect.width > this.maxWidth) {
			lockPosition = 1;
			rect.width = this.maxWidth;
			rect.height = (this.maxWidth / this.originRectangle.width) * this.originRectangle.height;
		}
		if (lockPosition === -1) {
			rect.x = rect.x - Math.abs(this.renderRectangle.width * this.scale - rect.width) / 2;
			rect.y = rect.y - Math.abs(this.renderRectangle.height * this.scale - rect.height) / 2;
		}
		if (lockPosition === 1) {
			rect.x = rect.x + Math.abs(this.renderRectangle.width * this.scale - rect.width) / 2;
			rect.y = rect.y + Math.abs(this.renderRectangle.height * this.scale - rect.height) / 2;
		}
		const min = {
			x: -rect.height + this.gap,
			y: -rect.width + this.gap,
		};
		const max = {
			x: this.bounds.width - this.gap,
			y: this.bounds.height - this.gap,
		};

		rect.x = rect.x > max.x ? max.x : rect.x < min.x ? min.x : rect.x;
		rect.y = rect.y > max.y ? max.y : rect.y < min.y ? min.y : rect.y;

		this.prevPos = { x: rect.x, y: rect.y };
		return rect;
	}

	private getDistance(pointA, pointB) {
		const xDiff = pointA.x - pointB.x;
		const yDiff = pointA.y - pointB.y;
		return Math.sqrt(xDiff * xDiff + yDiff * yDiff);
	}
}
