import { Point } from '../commonTypes';
const sin = Math.sin,
	cos = Math.cos,
	acos = Math.acos,
	atan2 = Math.atan2,
	pi = Math.PI,
	tau = 2 * pi,
	crt = v => {
		if (v < 0) return -Math.pow(-v, 1 / 3);
		return Math.pow(v, 1 / 3);
	},
	sqrt = v => Math.sqrt(v);

function align(curve, line) {
	const tx = line.p1.x,
		ty = line.p1.y,
		a = -atan2(line.p2.y - ty, line.p2.x - tx);
	return curve.map(v => {
		return {
			x: (v.x - tx) * cos(a) - (v.y - ty) * sin(a),
			y: (v.x - tx) * sin(a) + (v.y - ty) * cos(a),
		};
	});
}

// Cardano's algorithm, based on http://www.trans4mind.com/personal_development/mathematics/polynomials/cubicAlgebra.htm
function cardano(curve, line) {
	line = line || { p1: { x: 0, y: 0 }, p2: { x: 1, y: 0 } };
	// align curve with the intersecting line:
	const aligned = align(curve, line),
		// and rewrite from [a(1-t)^3 + 3bt(1-t)^2 + 3c(1-t)t^2 + dt^3] form
		pa = aligned[0].y,
		pb = aligned[1].y,
		pc = aligned[2].y,
		pd = aligned[3].y,
		// to [t^3 + at^2 + bt + c] form:
		d = -pa + 3 * pb - 3 * pc + pd || 1,
		a = (3 * pa - 6 * pb + 3 * pc) / d,
		b = (-3 * pa + 3 * pb) / d,
		c = pa / d,
		// then, determine p and q:
		p = (3 * b - a * a) / 3,
		p3 = p / 3,
		q = (2 * a * a * a - 9 * a * b + 27 * c) / 27,
		q2 = q / 2,
		// and determine the discriminant:
		discriminant = q2 * q2 + p3 * p3 * p3;
	// and some reserved variables
	let u1, v1, x1, x2, x3;

	// If the discriminant is negative, use polar coordinates
	// to get around square roots of negative numbers
	if (discriminant < 0) {
		//console.log("<0");
		const mp3 = -p / 3,
			mp33 = mp3 * mp3 * mp3,
			r = sqrt(mp33),
			// compute cosphi corrected for IEEE float rounding:
			t = -q / (2 * r),
			cosphi = t < -1 ? -1 : t > 1 ? 1 : t,
			phi = acos(cosphi),
			crtr = crt(r),
			t1 = 2 * crtr;
		x1 = t1 * cos(phi / 3) - a / 3;
		x2 = t1 * cos((phi + tau) / 3) - a / 3;
		x3 = t1 * cos((phi + 2 * tau) / 3) - a / 3;
		return [x1, x2, x3];
	} else if (discriminant === 0) {
		//console.log("=0");
		u1 = q2 < 0 ? crt(-q2) : -crt(q2);
		x1 = 2 * u1 - a / 3;
		x2 = -u1 - a / 3;
		return [x1, x2];
	}

	// one real root, and two imaginary roots
	else {
		const sd = sqrt(discriminant),
			// tt = -q2 + sd;
			u1 = crt(-q2 + sd);
		v1 = crt(q2 + sd);
		x1 = u1 - v1 - a / 3;
		return [x1];
	}
}

export const getBezierIntersections = (
	curve: {
		x: number | undefined;
		y: number | undefined;
	}[],
	line: { p1: Point; p2: Point }
) => {
	function get(t, a, b, c, d) {
		return a * (1 - t) * (1 - t) * (1 - t) + 3 * b * (1 - t) * (1 - t) * t + 3 * c * (1 - t) * t * t + d * t * t * t;
	}
	const roots = cardano(curve, line);
	return roots.map(t => {
		return {
			x: get(t, curve[0].x, curve[1].x, curve[2].x, curve[3].x),
			y: get(t, curve[0].y, curve[1].y, curve[2].y, curve[3].y),
		};
	});
};
