/* eslint-disable node/no-unsupported-features/es-syntax */

import { useEffect, useRef, useState } from 'react';
import anime from 'animejs/lib/anime.es.js';
import { Rect, Size, Point } from '../commonTypes';
import './curveeditor.css';
import { Projection, ProjectionStatus } from './projection';

const isFirefox = navigator.userAgent.search('Firefox') > -1;
const isChrome = /Chrome/.test(navigator.userAgent) && /Google Inc/.test(navigator.vendor);

interface ownProps {
	displayRect: Rect;
	originRect: Rect;
	maxScale?: number;
	minScale?: number;
	cursor?: string | null;
	reset?: number; // if you change this number the reset animation occures
	zoomIn?: number; // if you change this number zoom in occures
	zoomOut?: number; // if you change this number zoom Out occures
	onChange: (renderRect: Rect) => void;
	onStatus?: (status: ProjectionStatus) => void;
	onMouseDown?: (pos: Point) => void;
	onMouseUp?: (pos: Point) => void;
	onMouseMove?: (pos: Point) => void;
}

const ProjectionSurface = (props: ownProps) => {
	const {
		displayRect,
		originRect,
		maxScale,
		minScale,
		cursor,
		reset,
		zoomIn,
		zoomOut,
		onChange,
		onStatus,
		onMouseDown: externalMouseDown = () => null,
		onMouseUp: externalMouseUp = () => null,
		onMouseMove: externalMouseMove = () => null,
	} = props;

	// references for out-of-scope functions
	const display = useRef<Rect>(displayRect);
	display.current = displayRect;
	const origin = useRef<Rect>(originRect);
	origin.current = originRect;

	const status = useRef<ProjectionStatus>({ pan: false, zoom: false });
	const surfaceRef = useRef<HTMLCanvasElement | null>(null);
	const projectionRect = useRef<Rect>({ x: 0, y: 0, width: 0, height: 0 });
	const projection = useRef(new Projection());
	const spaceDown = useRef(false);
	const mouseDown = useRef(false);
	const mouseDownStartPos = useRef<Point | null>(null);
	const mousePos = useRef<Point>({ x: 0, y: 0 });
	const touchAnchor = useRef<Point | null>(null);
	const zoomStartPos = useRef<Point>({ x: 0, y: 0 });
	const zoomResetTween = useRef<anime | null>(null);
	const zoomResetRect = useRef<Rect | null>(null);
	const [localCursor, setLocalCursor] = useState<string | null>(null);

	//
	// effects
	//
	useEffect(() => {
		document.addEventListener('mouseup', onMouseUp);
		document.addEventListener('mousemove', onMouseMove);
		document.addEventListener('touchend', onTouchUp);
		document.addEventListener('touchmove', onTouchMove);
		document.addEventListener('wheel', onWheel, { passive: false });
		document.addEventListener('keydown', onKeyDown);
		document.addEventListener('keyup', onKeyUp);

		if (surfaceRef.current) {
			// mousedown and touchstart has to be a passive event listener so propagation can be canceled
			surfaceRef.current.addEventListener('touchstart', onTouchDown, { passive: false });
			surfaceRef.current.addEventListener('mousedown', onMouseDown, { passive: false });
		}

		return () => {
			document.removeEventListener('mouseup', onMouseUp);
			document.removeEventListener('mousemove', onMouseMove);
			document.removeEventListener('touchend', onTouchUp);
			document.removeEventListener('touchmove', onTouchMove);
			document.removeEventListener('wheel', onWheel);
			document.removeEventListener('keydown', onKeyDown);
			document.removeEventListener('keyup', onKeyUp);
			if (surfaceRef.current) {
				surfaceRef.current.removeEventListener('touchstart', onTouchDown);
				surfaceRef.current.removeEventListener('mousedown', onMouseDown);
			}
		};
	}, []);

	useEffect(() => {
		if (display.current.width && display.current.height) {
			resetProjection();
		}
	}, [reset]);

	useEffect(() => {
		multiplyZoom(1);
	}, [zoomIn]);

	useEffect(() => {
		multiplyZoom(-1);
	}, [zoomOut]);

	useEffect(() => {
		projection.current.resetBounds({ ...displayRect });
		projection.current.setOriginRectangle({ ...originRect }, false);
		projection.current.setScaleEdges({
			min: minScale === undefined ? projection.current.scaleEdges.min : minScale,
			max: maxScale === undefined ? projection.current.scaleEdges.max : maxScale,
		});
		update();
	}, [displayRect, originRect]);

	const update = () => {
		projectionRect.current = projection.current.getRenderRectangle();
		onChange(projectionRect.current);
	};

	const setStatus = (newStatus: { pan?: boolean; zoom?: boolean }) => {
		status.current = { ...status.current, ...newStatus };
		if (onStatus) onStatus(status.current);
	};

	const onSurfaceRef = ref => {
		if (ref && !surfaceRef.current) {
			surfaceRef.current = ref!;
			surfaceRef.current?.focus();
		}
	};

	const onTouchMove = (evt: TouchEvent) => {
		const points: Point[] = [];
		for (let i = 0; i < evt.targetTouches.length; i++) {
			const { pageX, pageY } = evt.targetTouches[i];
			points.push({
				x: pageX - display.current.x,
				y: pageY - display.current.y,
			});
		}
		projectionMove(points);
		externalMouseMove({ x: evt.touches[0].pageX - display.current.x, y: evt.touches[0].pageY - display.current.y });
	};

	const onMouseMove = (evt: MouseEvent) => {
		projectionMove([{ x: evt.pageX - display.current.x, y: evt.pageY - display.current.y }]);
		externalMouseMove({ x: evt.pageX - display.current.x, y: evt.pageY - display.current.y });
	};

	const onTouchDown = (evt: TouchEvent) => {
		evt.preventDefault();
		evt.stopPropagation();
		const points: Point[] = [];
		for (let i = 0; i < evt.targetTouches.length; i++) {
			const { pageX, pageY } = evt.targetTouches[i];
			points.push({
				x: pageX - display.current.x,
				y: pageY - display.current.y,
			});
		}
		projectionDown(points, true);
		externalMouseDown({ x: evt.touches[0].pageX - display.current.x, y: evt.touches[0].pageY - display.current.y });
	};

	const onMouseDown = (evt: MouseEvent) => {
		projectionDown([{ x: evt.pageX - display.current.x, y: evt.pageY - display.current.y }], false, evt.which);
		externalMouseDown({ x: evt.pageX - display.current.x, y: evt.pageY - display.current.y });
	};

	const onTouchUp = (evt: TouchEvent) => {
		const points: Point[] = [];
		if (evt.targetTouches.length) {
			for (let i = 0; i < evt.targetTouches.length; i++) {
				const { pageX, pageY } = evt.targetTouches[i];
				points.push({
					x: pageX - display.current.x,
					y: pageY - display.current.y,
				});
			}
			projectionUp(points);
		}
		externalMouseUp({ x: evt.touches[0].pageX - display.current.x, y: evt.touches[0].pageY - display.current.y });
	};

	const onMouseUp = (evt: MouseEvent) => {
		projectionUp([{ x: evt.pageX - display.current.x, y: evt.pageY - display.current.y }]);
		externalMouseUp({ x: evt.pageX - display.current.x, y: evt.pageY - display.current.y });
	};

	const onWheel = (evt: WheelEvent) => {
		// inverse scroll only works under Safari
		let scrollMultiplier = evt['webkitDirectionInvertedFromDevice'] === false ? -1 : 1;
		if (isChrome) {
			// Chrome seem to scroll twice as fast
			scrollMultiplier *= 0.5;
		}

		const realDelta = {
			x: evt.deltaX * scrollMultiplier,
			y: evt.deltaY * scrollMultiplier,
		};
		if (evt.target === surfaceRef.current) {
			// projection stuff start
			const touchpad = projection.current.isTouchPad(evt);
			let pinchTrackpadZoom = false;
			const { ctrlKey } = evt;
			if (ctrlKey) {
				pinchTrackpadZoom = true;
			}
			// Firefox doesn't detect mouse on a touch device, so we initiate zoom;
			if (touchpad && !pinchTrackpadZoom && !isFirefox) {
				startPan();
				projection.current.updatePan({
					x: mousePos.current.x - realDelta.x,
					y: mousePos.current.y - realDelta.y,
				});
				stopPan();
			} else {
				const staticGap = 100;
				const maxDelta = 40;
				const delta = realDelta.y > maxDelta ? maxDelta : realDelta.y < -maxDelta ? -maxDelta : evt.deltaY;
				const virtualMouseMove = delta / 2;
				const mousePosition = {
					x: evt.pageX,
					y: evt.pageY,
				};
				projection.current.startZoom(mousePosition, {
					x: mousePosition.x,
					y: mousePosition.y + staticGap,
				});
				projection.current.updateZoom(mousePosition, {
					x: mousePosition.x,
					y: mousePosition.y + staticGap + virtualMouseMove * (pinchTrackpadZoom ? -1 : 1),
				});
				projection.current.stopZoom();
			}
			update();
			// projection end

			evt.preventDefault();
		}
	};

	//
	// Zoom and Pan helpers
	//
	function onKeyDown(evt: KeyboardEvent) {
		if (evt.repeat) {
			return;
		}
		switch (evt.code) {
			case 'Space':
				spaceDown.current = true;
				setLocalCursor('grab');
				break;
		}
	}
	function onKeyUp(evt: KeyboardEvent) {
		if (evt.repeat) {
			return;
		}
		switch (evt.code) {
			case 'Space':
				spaceDown.current = false;
				if (surfaceRef.current && !cursor) {
					setLocalCursor(null);
				}
				if (mouseDown.current) {
					stopPan();
				}
				break;
		}
	}
	const registerMouse = (points: Point[]) => {
		if (points[0]) {
			mousePos.current = points[0];
		}
		if (points[1]) {
			touchAnchor.current = {
				x: points[0].x + (points[1].x - points[0].x) / 2,
				y: points[0].y + (points[1].y - points[0].y) / 2,
			};
		} else {
			touchAnchor.current = null;
		}
	};

	const startZoom = () => {
		zoomStartPos.current = {
			x: mousePos.current.x,
			y: mousePos.current.y,
		};
		projection.current.startZoom(touchAnchor.current || zoomStartPos.current, mousePos.current);
		setStatus({ zoom: true });
	};

	function stopZoom() {
		projection.current.stopZoom();
		setStatus({ zoom: false });
	}

	const startPan = () => {
		projection.current.startPan(mousePos.current);
		setStatus({ pan: true });
	};

	const stopPan = () => {
		projection.current.stopPan();
		setStatus({ pan: false });
	};

	const handleZoom = () => {
		if (status.current.zoom) {
			projection.current.updateZoom(touchAnchor.current || zoomStartPos.current, mousePos.current);
			update();
		} else if (status.current.pan) {
			projection.current.updatePan(mousePos.current);
			update();
		}
	};

	const projectionDown = (points, touch, buttonWhich?) => {
		if (touch || buttonWhich === 1) {
			mouseDown.current = true;
			mouseDownStartPos.current = points[0];
			registerMouse(points);
			if (points.length === 2 && touch) {
				startZoom();
			} else if (spaceDown.current) {
				startPan();
			}
		}
		if (buttonWhich === 2 || buttonWhich === 3) {
			startPan();
		}
	};

	const projectionMove = points => {
		registerMouse(points);
		if (mouseDownStartPos.current && (status.current.zoom || status.current.pan)) {
			handleZoom();
		}
	};

	const projectionUp = points => {
		mouseDown.current = false;
		mouseDownStartPos.current = null;
		registerMouse(points);
		stopZoom();
		stopPan();
	};

	const resetProjection = () => {
		if (zoomResetTween.current) {
			zoomResetTween.current.pause();
		}
		zoomResetRect.current = { ...projectionRect.current };
		const { x, y, width, height } = origin.current;
		zoomResetTween.current = anime({
			targets: zoomResetRect.current,
			x: x,
			y: y,
			width: width,
			height: height,
			duration: 400,
			easing: 'cubicBezier(0.2, 0, 0.2, 1)',
			update: () => {
				if (zoomResetRect.current) {
					projection.current.setRenderRectangle(zoomResetRect.current);
				}
				update();
			},
			complete: () => {
				zoomResetRect.current = null;
				zoomResetTween.current = null;
				update();
			},
		});
	};

	const multiplyZoom = (amount: number) => {
		const { x, y, height, width } = projectionRect.current;
		const originDistance = {
			x: amount > 0 ? 0 : origin.current.x + origin.current.width / 2 - (x + width / 2),
			y: amount > 0 ? 0 : origin.current.y + origin.current.height / 2 - (y + height / 2),
		};
		const center = { x: x + width / 2, y: y + height / 2 };
		const scale = projection.current.getScale();
		const zoomAmount = amount * scale * 50;
		const size: Size = {
			width: width + zoomAmount,
			height: height * ((width + zoomAmount) / width),
		};
		projection.current.setRenderRectangle({
			x: center.x + originDistance.x * 0.2 - size.width / 2,
			y: center.y + originDistance.y * 0.2 - size.height / 2,
			width: size.width,
			height: size.height,
		});
		update();
	};

	return (
		<div
			ref={onSurfaceRef}
			style={{
				cursor: localCursor || cursor || 'auto',
				position: 'absolute',
				left: 0,
				top: 0,
				width: display.current.width,
				height: display.current.height,
			}}
		></div>
	);
};

export default ProjectionSurface;
