/* eslint-disable node/no-unsupported-features/es-syntax */

import { useEffect, useRef, useState } from 'react';
import { ActivePoint, DrawKeyframe, Ease, EdgeNumbers, Keyframe, KeyframeAspect, KeyframeType } from '../animationTypes';
import { Rect, Point } from '../commonTypes';
import { getBezierIntersections } from './bezier';
import './curveeditor.css';
import { ProjectionStatus } from './projection';
import { EasingEditorTheme } from './types';

import {
	closestPointOnCurve,
	drawCircle,
	drawKeyframesOnCanvas,
	drawRect,
	drawTimeElements,
	getDistance,
	getPercentByValue,
	getValueByPercent,
	mapPoint,
	pointUnderCursor,
	splitCurve,
} from './utils';
import SvgCurve from './SvgCurve';
import ProjectionSurface from './ProjectionSurface';

const projectionGap = window.innerWidth > 800 ? 50 : 20;

interface ownProps {
	theme: EasingEditorTheme;
	keyframes: Keyframe[];
	valueMap: {
		min: number; // DataTypes;
		max: number; // DataTypes;
	};
	timeMap: EdgeNumbers;
	centered?: boolean;
}

const CurveEditor = (props: ownProps) => {
	const { theme, keyframes, timeMap, valueMap, centered } = props;

	// change this number to force render
	const [, setUpdateView] = useState(0);
	// change this number to reset view
	const [resetView, setResetView] = useState(0);
	// change this number to zoom in
	const [zoomIn, setZoomIn] = useState(0);
	// change this number to zoom out
	const [zoomOut, setZoomOut] = useState(0);
	// passes cursor to projection
	const [cursorOverride, setCursorOverride] = useState<string | null>(null);
	// cursor for out of scope functions
	const cursorOverrideRef = useRef<string | null>(null);
	cursorOverrideRef.current = cursorOverride;

	const editedKeyframeRef = useRef<ActivePoint | null>(null);
	const activeKeyframeRef = useRef<ActivePoint | null>(null);
	const containerRef = useRef<HTMLDivElement | null>(null);
	const canvasRef = useRef<HTMLCanvasElement | null>(null);
	const ctxRef = useRef<CanvasRenderingContext2D | null>(null);
	const resizeObserver = useRef<ResizeObserver | null>(null);
	const displayRect = useRef<Rect>({ x: 0, y: 0, width: 0, height: 0 });
	const draw = useRef(false);
	const seek = useRef(false);
	const tempKeyframes = useRef<Keyframe[]>(keyframes);
	const curveKeyframes = useRef<DrawKeyframe[]>([]);
	const drawKeframes = useRef<DrawKeyframe[]>([]);
	const updateCanvasRect = useRef(false);
	const requestFrameId = useRef(0);
	const projectionOrigin = useRef<Rect>({ x: 0, y: 0, width: 0, height: 0 });
	const projectionRect = useRef<Rect>({ x: 0, y: 0, width: 0, height: 0 });
	const valueMapRect = useRef<Rect>({ x: 0, y: 0, width: 0, height: 0 });
	const valueMapProjectionRect = useRef<Rect>({ x: 0, y: 0, width: 0, height: 0 });
	const activePointPosition = useRef<Point | null>(null);
	const mouseDownPos = useRef<Point | null>(null);
	const mouseDownOffset = useRef<Point | null>(null);
	const mousePos = useRef<Point>({ x: 0, y: 0 });
	const projectionStatus = useRef<ProjectionStatus>({ pan: false, zoom: false });

	// demonstration variables
	const timePercent = useRef<number>(0);
	const isSeeking = useRef(false);
	const seekPosition = useRef(0);
	const actCurveIdx = useRef<number>(-1);
	const actEase = useRef<Ease | null>(null);
	const projectionValuePercent = useRef<number>(0);
	const valuePercent = useRef<number>(0);
	const closestPoint = useRef<Point | null>(null);
	const closestCurveIdx = useRef<number | null>(null);
	const addDemoPoints = useRef(false);
	const [showInfoPanel, setShowInfoPanel] = useState(false);

	// projection
	const spaceDown = useRef(false);
	const altDown = useRef(false);
	const shiftDown = useRef(false);
	const isDefaultPosition = useRef(true);
	const zoomButtonInterval = useRef<number>(0);
	const continousZoomInterval = useRef<number>(0);

	// svg drawing option
	const drawSvg = useRef(false);
	const [, setUpdateSvg] = useState(0);

	//
	// effects
	//

	useEffect(() => {
		addResizeObserver();
		document.addEventListener('keydown', onKeyDown);
		document.addEventListener('keyup', onKeyUp);
		tick();

		return () => {
			removeResizeObserver();
			document.removeEventListener('keydown', onKeyDown);
			document.removeEventListener('keyup', onKeyUp);
			cancelAnimationFrame(requestFrameId.current);
		};
	}, []);

	useEffect(() => {
		tempKeyframes.current = keyframes;
	}, [keyframes]);

	useEffect(() => {
		allowToDraw();
	}, [keyframes, theme]);

	useEffect(() => {
		valueMapRect.current = {
			x: timeMap.min,
			y: valueMap.min,
			width: timeMap.max - timeMap.min,
			height: valueMap.max - valueMap.min,
		};
		valueMapProjectionRect.current = {
			x: valueMapRect.current.x,
			y: valueMapRect.current.y + valueMapRect.current.height,
			width: valueMapRect.current.width,
			height: -valueMapRect.current.height,
		};
		allowToDraw();
	}, [valueMap, timeMap]);
	//
	// utils
	//
	const onContainerRef = ref => {
		if (ref && !containerRef.current) {
			containerRef.current = ref;
		}
	};

	const onCanvasRef = ref => {
		if (ref && !canvasRef.current) {
			canvasRef.current = ref!;
			canvasRef.current?.focus();
			ctxRef.current = ref.getContext('2d');
			if (ctxRef.current) {
				ctxRef.current.imageSmoothingEnabled = true;
				ctxRef.current.imageSmoothingQuality = 'high';
				ctxRef.current.lineCap = 'round';
			}
		}
	};

	const allowToDraw = () => {
		draw.current = true;
		if (drawSvg.current) {
			// TODO: this state update can be processor intence here, consider put it in tick() function
			setUpdateSvg(x => x + 1);
		}
	};

	const tick = () => {
		if (addDemoPoints.current) {
			handleDemoPoint();
			setUpdateView(x => x + 1);
		}
		if (canvasRef.current && updateCanvasRect.current) {
			const { devicePixelRatio } = window;
			const { width, height } = displayRect.current;
			canvasRef.current.width = width * devicePixelRatio;
			canvasRef.current.height = height * devicePixelRatio;
			if (ctxRef.current) {
				ctxRef.current.scale(devicePixelRatio, devicePixelRatio);
				canvasRef.current.style.width = width + 'px';
				canvasRef.current.style.height = height + 'px';
			}
			setUpdateView(x => x + 1);
			updateCanvasRect.current = false;
			draw.current = true;
		}
		if (draw.current) {
			handleDraw();
			updateProjectionButtons();
			draw.current = false;
		}
		requestFrameId.current = requestAnimationFrame(tick);
	};

	//
	// Draw keyframes
	//
	const handleDraw = () => {
		// convert all drawn points { x:time, y: value }
		// TODO: these closest static keyframes would be defined by code search
		const prevStaticFrame = tempKeyframes.current[0];
		const nextStaticFrame = tempKeyframes.current[tempKeyframes.current.length - 1];
		const staticKeyframes: Keyframe[] = tempKeyframes.current.map((keyframe: Keyframe) => {
			if (keyframe.type === KeyframeType.Static) {
				return keyframe;
			} else {
				const { value, time } = keyframe;
				return {
					...keyframe,
					value: getValueByPercent(prevStaticFrame.value, nextStaticFrame.value, value),
					time: getValueByPercent(prevStaticFrame.time, nextStaticFrame.time, time),
				};
			}
		});

		curveKeyframes.current = staticKeyframes
			.map((keyframe: Keyframe, idx: number, arr: Keyframe[]) => {
				const { ease, type } = keyframe;
				const nextKeyframe = arr[idx + 1];
				if (!nextKeyframe) {
					return {
						start: { x: keyframe.time, y: keyframe.value },
						end: null,
						startControl: null,
						endControl: null,
						type,
					};
				} else {
					return {
						start: { x: keyframe.time, y: keyframe.value },
						end: { x: nextKeyframe.time, y: nextKeyframe.value },
						startControl: {
							x: ease ? getValueByPercent(prevStaticFrame.time, nextStaticFrame.time, ease[0]) : null,
							y: ease ? getValueByPercent(prevStaticFrame.value, nextStaticFrame.value, ease[1]) : null,
						},
						endControl: {
							x: ease ? getValueByPercent(prevStaticFrame.time, nextStaticFrame.time, ease[2]) : null,
							y: ease ? getValueByPercent(prevStaticFrame.value, nextStaticFrame.value, ease[3]) : null,
						},
						type,
					};
				}
			})
			.filter(x => !!x.start);
		// map curve points to drawable coordinates
		drawKeframes.current = curveKeyframes.current.map((keyframe: DrawKeyframe) => {
			const { start, end, startControl, endControl } = keyframe;

			const newKeyframe = {
				...keyframe,
				start: mapPoint(start, valueMapProjectionRect.current, projectionRect.current),
				end: mapPoint(end, valueMapProjectionRect.current, projectionRect.current),
				startControl: mapPoint(startControl, valueMapProjectionRect.current, projectionRect.current),
				endControl: mapPoint(endControl, valueMapProjectionRect.current, projectionRect.current),
			};

			return newKeyframe;
		});
		if (seek.current) {
			onSeek(seekPosition.current);
			seek.current = false;
		}
		// drawing
		if (ctxRef.current && canvasRef.current && projectionOrigin.current) {
			ctxRef.current.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height);
			drawRect({ ctx: ctxRef.current, rect: projectionOrigin.current, color: '#7d7d7d', lineWidth: theme.uiLineWidth / 2.7 });
			if (!drawSvg.current) {
				drawRect({ ctx: ctxRef.current, rect: projectionRect.current, color: theme.uiColor, lineWidth: theme.uiLineWidth });
				drawKeyframesOnCanvas(ctxRef.current, drawKeframes.current, theme, !!editedKeyframeRef.current);
			}
			drawTimeElements(ctxRef.current, projectionOrigin.current, timePercent.current, projectionValuePercent.current, valuePercent.current);
			if (closestPoint.current) {
				drawCircle({ ctx: ctxRef.current, center: closestPoint.current, radius: 4, color: 'white' });
			}
		}
	};

	//
	// Mouse interactions
	//
	const setCursor = (cursor: string) => {
		if (cursorOverrideRef.current !== cursor) {
			setCursorOverride(cursor);
		}
	};

	const onSeek = (pixelX: number) => {
		const proj = projectionOrigin.current;
		const projRect = projectionRect.current;
		if (proj) {
			timePercent.current = Math.min(1, Math.max(0, getPercentByValue(proj.x, proj.x + proj.width, pixelX)));
			const playheadPos = proj.x + proj.width * timePercent.current;
			actCurveIdx.current = drawKeframes.current.reduce((acc: number, keyframe: DrawKeyframe, idx: number) => {
				return keyframe && keyframe.start && keyframe.start.x <= playheadPos ? idx : acc;
			}, -1);

			const maxX = drawKeframes.current[drawKeframes.current.length - 1][KeyframeAspect.Start]!.x;
			actCurveIdx.current = playheadPos > maxX ? -1 : actCurveIdx.current;
			const ease = actCurveIdx.current >= 0 ? tempKeyframes.current[actCurveIdx.current].ease : null;
			if (ease) {
				actEase.current = ease;
				const drawFrame: DrawKeyframe = drawKeframes.current[actCurveIdx.current];
				const curve = [
					{ x: drawFrame.start?.x, y: drawFrame.start?.y },
					{ x: drawFrame.startControl?.x, y: drawFrame.startControl?.y },
					{ x: drawFrame.endControl?.x, y: drawFrame.endControl?.y },
					{ x: drawFrame.end?.x, y: drawFrame.end?.y },
				];
				const line = { p1: { x: playheadPos, y: -10000 }, p2: { x: playheadPos, y: 10000 } };
				const curvePoints = getBezierIntersections(curve, line);
				if (curvePoints && curvePoints.length) {
					projectionValuePercent.current = getPercentByValue(proj.y, proj.y + proj.height, curvePoints[curvePoints.length - 1].y);
					valuePercent.current = getPercentByValue(projRect.y + projRect.height, projRect.y, curvePoints[curvePoints.length - 1].y);
				}
			}
		}
	};

	const handleMouseMove = (localMouse: Point) => {
		mousePos.current = { ...localMouse };
		if (shiftDown.current) {
			const closestData: { point: Point | null; idx: number } = closestPointOnCurve(drawKeframes.current, mousePos.current);
			closestPoint.current = closestData.point;
			closestCurveIdx.current = closestData.idx;
			if (closestPoint.current) {
				allowToDraw();
			}
			return;
		}
		if (projectionStatus.current.pan || projectionStatus.current.zoom) {
			seek.current = true;
			allowToDraw();
			return;
		}
		if (!isSeeking.current && !activeKeyframeRef.current) {
			const active: ActivePoint | null = pointUnderCursor(mousePos.current, drawKeframes.current, theme);
			if (canvasRef.current && !spaceDown.current) {
				if (active) {
					if (altDown.current) {
						if (active.aspect === KeyframeAspect.Start) {
							if (tempKeyframes.current[active.idx].type === KeyframeType.Static) {
								setCursor('pointer');
							} else {
								setCursor('crosshair');
							}
						}
						if (active.aspect === KeyframeAspect.StartControl || active.aspect === KeyframeAspect.EndControl) {
							setCursor('all-scroll');
						}
					} else {
						setCursor('pointer');
					}
				} else if (
					projectionOrigin.current &&
					mousePos.current.x > projectionOrigin.current.x &&
					mousePos.current.x < projectionOrigin.current.x + projectionOrigin.current.width
				) {
					setCursor('col-resize');
				} else {
					setCursor('auto');
				}
			}
			return;
		}
		if (isSeeking.current && projectionOrigin.current) {
			// presentation purposes
			if (isSeeking.current && drawKeframes.current) {
				seekPosition.current = mousePos.current.x;
				seek.current = true;
			}
			// presentation over
			allowToDraw();
			return;
		}

		// drag things
		if (activeKeyframeRef.current && mouseDownPos.current && mouseDownOffset.current && valueMapRect.current) {
			const temp = tempKeyframes.current;
			const { idx: activeIdx, aspect } = activeKeyframeRef.current;
			const { x: offsetX, y: offsetY } = mouseDownOffset.current;
			const keyframe: Keyframe = temp[activeIdx];

			const prevKeyframe: Keyframe = temp[activeIdx - 1];
			const nextKeyframe: Keyframe = temp[activeIdx + 1];
			// TODO: these closest static keyframes would be defined by code search
			const staticStartIdx = 0;
			const staticEndIdx = temp.length - 1;
			const prevStaticKeyframe: Keyframe = temp[staticStartIdx];
			const nextStaticKeyframe: Keyframe = temp[staticEndIdx];

			const activePosition: Point = { x: localMouse.x - offsetX, y: localMouse.y - offsetY };
			// convert it to a point where x: time, y: value
			const valuePoint = mapPoint(activePosition, projectionRect.current, valueMapProjectionRect.current)!;

			// the editor only allows to edit start positions, not the end ones
			if (aspect === KeyframeAspect.Start) {
				const minTime: number = prevKeyframe
					? prevKeyframe.type === KeyframeType.Static
						? prevStaticKeyframe.time
						: getValueByPercent(prevStaticKeyframe.time, nextStaticKeyframe.time, prevKeyframe.time)
					: valueMapRect.current.x;

				const maxTime: number = nextKeyframe
					? nextKeyframe.type === KeyframeType.Static
						? nextStaticKeyframe.time
						: getValueByPercent(prevStaticKeyframe.time, nextStaticKeyframe.time, nextKeyframe.time)
					: valueMapRect.current.x + valueMapRect.current.width;

				const timeEdges: EdgeNumbers = {
					min: minTime,
					max: maxTime,
				};

				const newKeyframe: Keyframe = {
					...keyframe,
					time: Math.min(Math.max(valuePoint.x, timeEdges.min), timeEdges.max),
					value:
						keyframe.type === KeyframeType.Relative
							? valuePoint.y
							: Math.min(valueMapRect.current.y + valueMapRect.current.height, Math.max(valuePoint.y, valueMapRect.current.y)),
				};

				// in case of relative keyframe convert time and value to be relative to the previous and next static keyframes
				if (keyframe.type === KeyframeType.Relative && drawKeframes.current) {
					newKeyframe.time = getPercentByValue(prevStaticKeyframe.time, nextStaticKeyframe.time, newKeyframe.time);
					newKeyframe.value = getPercentByValue(prevStaticKeyframe.value, nextStaticKeyframe.value, newKeyframe.value);
				}

				const originalPercentPoint = {
					x: !activeIdx ? 0 : activeIdx === tempKeyframes.current.length - 1 ? 1 : keyframe.time,
					y: !activeIdx ? 0 : activeIdx === tempKeyframes.current.length - 1 ? 1 : keyframe.value,
				};

				const newPercentPoint = {
					x: !activeIdx ? 0 : activeIdx === tempKeyframes.current.length - 1 ? 1 : newKeyframe.time,
					y: !activeIdx ? 0 : activeIdx === tempKeyframes.current.length - 1 ? 1 : newKeyframe.value,
				};

				const shiftPoint = {
					x: newPercentPoint.x - originalPercentPoint.x,
					y: newPercentPoint.y - originalPercentPoint.y,
				};

				tempKeyframes.current = tempKeyframes.current.map((keyframe: Keyframe, idx: number) => {
					if (idx === activeIdx - 1) {
						const { ease } = keyframe;
						return {
							...keyframe,
							ease: !ease ? ease : [ease[0], ease[1], ease[2] + shiftPoint.x, ease[3] + shiftPoint.y],
						};
					}
					if (idx === activeIdx) {
						const { ease } = newKeyframe;
						return {
							...newKeyframe,
							ease: !ease ? ease : [ease[0] + shiftPoint.x, ease[1] + shiftPoint.y, ease[2], ease[3]],
						};
					}
					return keyframe;
				});
			}

			if (aspect === KeyframeAspect.StartControl || aspect === KeyframeAspect.EndControl) {
				const drawStart: Point | null = drawKeframes.current[staticStartIdx][KeyframeAspect.Start];
				const drawEnd: Point | null = drawKeframes.current[staticEndIdx][KeyframeAspect.Start];

				const controlPercentage: Point =
					drawStart && drawEnd
						? {
								x: (activePosition.x - drawStart.x) / (drawEnd.x - drawStart.x),
								y: (activePosition.y - drawStart.y) / (drawEnd.y - drawStart.y),
						  }
						: { x: 0, y: 0 };

				const { ease } = keyframe;
				const newKeyframe: Keyframe = {
					...keyframe,
					ease: ease
						? [
								aspect === KeyframeAspect.StartControl ? controlPercentage.x : ease[0],
								aspect === KeyframeAspect.StartControl ? controlPercentage.y : ease[1],
								aspect === KeyframeAspect.EndControl ? controlPercentage.x : ease[2],
								aspect === KeyframeAspect.EndControl ? controlPercentage.y : ease[3],
						  ]
						: null,
				};
				tempKeyframes.current = tempKeyframes.current.map((keyframe: Keyframe, idx: number) => (idx === activeIdx ? newKeyframe : keyframe));

				// move corresponding curve control point if Alt button isn't pressed
				if (
					!altDown.current &&
					((aspect === KeyframeAspect.StartControl && activeIdx !== staticStartIdx) || (aspect === KeyframeAspect.EndControl && activeIdx !== staticEndIdx - 1))
				) {
					const centerAspect = aspect === KeyframeAspect.StartControl ? KeyframeAspect.Start : KeyframeAspect.End;
					const center = drawKeframes.current[activeIdx][centerAspect]!;
					const angle = Math.atan2(center.y - activePosition.y, center.x - activePosition.x);
					const pairIdx = aspect === KeyframeAspect.StartControl ? activeIdx - 1 : activeIdx + 1;
					const pairAspect = aspect === KeyframeAspect.StartControl ? KeyframeAspect.EndControl : KeyframeAspect.StartControl;
					const controlPair = drawKeframes.current[pairIdx][pairAspect]!;
					const pairRadius = getDistance(center, controlPair);

					const pairPoint = {
						x: center.x + Math.cos(angle) * pairRadius,
						y: center.y + Math.sin(angle) * pairRadius,
					};

					if (drawStart && drawEnd) {
						const controlPercentage: Point = {
							x: (pairPoint.x - drawStart.x) / (drawEnd.x - drawStart.x),
							y: (pairPoint.y - drawStart.y) / (drawEnd.y - drawStart.y),
						};
						const pairKeyframe = tempKeyframes.current[pairIdx];
						const pairEase = pairKeyframe.ease!;

						const newKeyframe: Keyframe = {
							...pairKeyframe,
							ease: ease
								? [
										pairAspect === KeyframeAspect.StartControl ? controlPercentage.x : pairEase[0],
										pairAspect === KeyframeAspect.StartControl ? controlPercentage.y : pairEase[1],
										pairAspect === KeyframeAspect.EndControl ? controlPercentage.x : pairEase[2],
										pairAspect === KeyframeAspect.EndControl ? controlPercentage.y : pairEase[3],
								  ]
								: null,
						};
						tempKeyframes.current = tempKeyframes.current.map((keyframe: Keyframe, idx: number) => (idx === pairIdx ? newKeyframe : keyframe));
					}
				}
			}

			seek.current = true;
			allowToDraw();
		}
	};

	const handleMouseDown = (localMouse: Point) => {
		// TODO: offset with patent;
		if (projectionStatus.current.pan || projectionStatus.current.zoom) {
			return;
		}
		const mousePos = { ...localMouse };
		const active: ActivePoint | null = pointUnderCursor(mousePos, drawKeframes.current, theme);
		if (active) {
			if (altDown.current && active.aspect === KeyframeAspect.Start && tempKeyframes.current[active.idx].type !== KeyframeType.Static) {
				deleteKeyframe(active.idx);
				return;
			}
			isSeeking.current = false;
			activeKeyframeRef.current = active;
			editedKeyframeRef.current = active;
			mouseDownPos.current = mousePos;
			const pointCoordicales = drawKeframes.current[active.idx][active.aspect];
			if (pointCoordicales) {
				const { x: pointX, y: pointY } = pointCoordicales;
				mouseDownOffset.current = {
					x: mousePos.x - pointX,
					y: mousePos.y - pointY,
				};
			}
		} else {
			isSeeking.current = true;
			activeKeyframeRef.current = null;
			editedKeyframeRef.current = null;
			handleMouseMove(localMouse);
			allowToDraw();
		}
	};

	const deleteKeyframe = (idx: number) => {
		tempKeyframes.current.splice(idx, 1);
		allowToDraw();
	};

	const splitActiveCurve = () => {
		if (closestPoint.current && closestCurveIdx.current !== null) {
			const splittedCurves: [DrawKeyframe, DrawKeyframe] | null = splitCurve(drawKeframes.current[closestCurveIdx.current], closestPoint.current);
			if (splittedCurves) {
				// TODO: these closest static keyframes would be defined by code search
				const temp = tempKeyframes.current;
				const staticStartIdx = 0;
				const staticEndIdx = temp.length - 1;
				const prevStaticKeyframe: Keyframe = temp[staticStartIdx];
				const nextStaticKeyframe: Keyframe = temp[staticEndIdx];
				const prevStaticDrawFrame: DrawKeyframe = drawKeframes.current[staticStartIdx];
				const nextStaticDrawFrame: DrawKeyframe = drawKeframes.current[staticEndIdx];

				const splittedKeyframes: Keyframe[] = splittedCurves.map((drawFrame: DrawKeyframe, idx: number) => {
					let keyframe: Keyframe = {
						type: KeyframeType.Relative,
						time: 0,
						value: 0,
						ease: null,
					};
					if (!idx && closestCurveIdx.current !== null) {
						// in case of the first keyframe we copy the data from the original keyframe
						keyframe = { ...tempKeyframes.current[closestCurveIdx.current] };
					}
					const valuePoint = mapPoint(drawFrame.start, projectionRect.current, valueMapProjectionRect.current)!;
					keyframe.time =
						keyframe.type === KeyframeType.Static ? valuePoint.x : getPercentByValue(prevStaticKeyframe.time, nextStaticKeyframe.time, valuePoint.x);
					keyframe.value =
						keyframe.type === KeyframeType.Static ? valuePoint.y : getPercentByValue(prevStaticKeyframe.value, nextStaticKeyframe.value, valuePoint.y);

					keyframe.ease = [
						getPercentByValue(prevStaticDrawFrame.start!.x, nextStaticDrawFrame.start!.x, drawFrame.startControl!.x),
						getPercentByValue(prevStaticDrawFrame.start!.y, nextStaticDrawFrame.start!.y, drawFrame.startControl!.y),
						getPercentByValue(prevStaticDrawFrame.start!.x, nextStaticDrawFrame.start!.x, drawFrame.endControl!.x),
						getPercentByValue(prevStaticDrawFrame.start!.y, nextStaticDrawFrame.start!.y, drawFrame.endControl!.y),
					];
					return keyframe;
				});
				const newKeyframes = [...tempKeyframes.current];
				newKeyframes.splice(closestCurveIdx.current, 1, ...splittedKeyframes);
				tempKeyframes.current = newKeyframes;
				editedKeyframeRef.current = { idx: closestCurveIdx.current, aspect: KeyframeAspect.Start };
			}
		}
	};

	const handleMouseUp = (localMouse: Point) => {
		handleMouseMove(localMouse);
		if (shiftDown.current) {
			splitActiveCurve();
			allowToDraw();
		}
		activeKeyframeRef.current = null;
		mouseDownPos.current = null;
		activePointPosition.current = null;
		mouseDownOffset.current = null;
		isSeeking.current = false;
	};

	function onKeyDown(evt: KeyboardEvent) {
		if (evt.repeat) {
			return;
		}
		let doRender = false;
		switch (evt.key) {
			case ' ':
				spaceDown.current = true;
				doRender = true;
				break;
			case 'Alt':
				altDown.current = true;
				doRender = true;
				break;
			case 'Shift':
				shiftDown.current = true;
				setCursor('copy');
				doRender = true;
				break;
		}
		if (doRender) {
			handleMouseMove({ ...mousePos.current });
		}
	}

	function onKeyUp(evt: KeyboardEvent) {
		if (evt.repeat) {
			return;
		}
		let doRender = false;
		switch (evt.key) {
			case ' ':
				spaceDown.current = false;
				doRender = true;
				break;
			case 'Alt':
				altDown.current = false;
				doRender = true;
				break;
			case 'Shift':
				shiftDown.current = false;
				closestPoint.current = null;
				doRender = true;
				break;
		}
		if (doRender) {
			handleMouseMove({ ...mousePos.current });
			allowToDraw();
		}
	}

	const resetProjection = () => {
		setResetView(x => x + 1);
	};

	const updateProjectionButtons = () => {
		const isCentered =
			projectionOrigin.current.x === projectionRect.current.x &&
			projectionOrigin.current.y === projectionRect.current.y + projectionRect.current.height &&
			projectionOrigin.current.width === projectionRect.current.width &&
			projectionOrigin.current.height === -projectionRect.current.height;

		if (isDefaultPosition.current && !isCentered) {
			const resetButton = document.querySelector('.zee-curve-editor-reset');
			if (resetButton) {
				resetButton.classList.remove('blocked-button');
			}
			isDefaultPosition.current = false;
		}
		if (!isDefaultPosition.current && isCentered) {
			const resetButton = document.querySelector('.zee-curve-editor-reset');
			if (resetButton) {
				resetButton.classList.add('blocked-button');
			}
			isDefaultPosition.current = true;
		}
	};

	const stopZoomInterval = () => {
		clearTimeout(zoomButtonInterval.current);
		clearInterval(continousZoomInterval.current);
	};

	const startZoomInterval = (direction: number) => {
		stopZoomInterval();
		zoomButtonInterval.current = window.setTimeout(continousZoomRepeat, 500, [direction]);
	};

	const continousZoomRepeat = (directionArray: number[]) => {
		clearTimeout(zoomButtonInterval.current);
		continousZoomInterval.current = window.setInterval(() => {
			if (directionArray[0] > 0) {
				setZoomIn(x => x + 1);
			} else {
				setZoomOut(x => x + 1);
			}
		}, 20);
	};

	//
	// Resize
	//
	const onResize = (entries: ReadonlyArray<ResizeObserverEntry>) => {
		if (entries.length) {
			const bounds = entries[0].target.getBoundingClientRect();
			displayRect.current = { x: bounds.x, y: bounds.y, width: bounds.width, height: bounds.height };
			const { width, height } = displayRect.current;
			//TODO: make it dynamic
			const projectionSize = Math.min(width, height) - 2 * projectionGap - (width > height - 60 ? 60 : 0);
			if (centered) {
				projectionOrigin.current = { x: width / 2 - projectionSize / 2, y: height / 2 - projectionSize / 2, width: projectionSize, height: projectionSize };
			} else {
				projectionOrigin.current = { x: projectionGap, y: projectionGap, width, height };
			}
			updateCanvasRect.current = true;
		}
	};

	const addResizeObserver = () => {
		if (containerRef.current) {
			resizeObserver.current = new ResizeObserver(onResize);
			resizeObserver.current.observe(containerRef.current);
		}
	};

	const removeResizeObserver = () => {
		if (resizeObserver.current) {
			resizeObserver.current.disconnect();
		}
	};

	const projectionChange = (renderRect: Rect) => {
		projectionRect.current = renderRect;
		seek.current = true;
		allowToDraw();
	};

	const handleProjectionStatusChange = (status: ProjectionStatus) => {
		projectionStatus.current = { ...status };
		allowToDraw();
	};

	// presentation purposes
	const handleDemoPoint = () => {
		const rect = displayRect.current;
		shiftDown.current = true;
		handleMouseMove({ x: rect.x + Math.random() * rect.width, y: rect.y + Math.random() * rect.height });
		splitActiveCurve();
		shiftDown.current = false;
		draw.current = true;
	};

	return (
		<div ref={onContainerRef} className="zee-curve-editor-container">
			{drawSvg.current && (
				<SvgCurve
					key="svg"
					keyframes={drawKeframes.current}
					projectionRect={projectionRect.current}
					renderRect={{ x: 0, y: 0, width: window.innerWidth, height: window.innerHeight }}
					theme={theme}
					isEditable={!!editedKeyframeRef.current}
				/>
			)}
			<canvas key="canvas" ref={onCanvasRef}></canvas>
			<ProjectionSurface
				displayRect={displayRect.current}
				originRect={projectionOrigin.current}
				maxScale={4}
				minScale={0.1}
				cursor={cursorOverride}
				reset={resetView}
				zoomIn={zoomIn}
				zoomOut={zoomOut}
				onChange={projectionChange}
				onMouseDown={handleMouseDown}
				onMouseUp={handleMouseUp}
				onMouseMove={handleMouseMove}
				onStatus={handleProjectionStatusChange}
			/>
			<div className="zee-curve-editor-button zee-cog-button" onMouseDown={() => setShowInfoPanel(x => !x)}>
				⚙
			</div>
			<div className={`zee-curve-editor-info ${showInfoPanel ? 'show' : ''}`}>
				<div className="render-buttons">
					<div
						className={`zee-curve-editor-button ${drawSvg.current ? 'inactive-button' : ''}`}
						onMouseDown={() => {
							drawSvg.current = false;
							setUpdateSvg(x => x + 1);
							allowToDraw();
						}}
					>
						Canvas curve rendering
					</div>
					<div
						className={`zee-curve-editor-button ${!drawSvg.current ? 'inactive-button' : ''}`}
						onMouseDown={() => {
							drawSvg.current = true;
							setUpdateSvg(x => x + 1);
							allowToDraw();
						}}
					>
						SVG curve rendering
					</div>
				</div>
				<div key="info-panel" className="zee-curve-editor-info-panel">
					Two finger tap to Pan and Zoom on mobiles
					<br />
					Space + Drag = Panning
					<br />
					Trackpad scroll = Panning
					<br />
					Scroll or Pinch = Zooming;
					<br />
					Shift + Click = Add anchor point
					<br />
					Alt + Anchor Click = Remove anchor point
					<br />
					Alt + Control point drag = Free curve
					<br />
					<br />
					<br />
					The curve contains {tempKeyframes.current.length} anchor points
					<div
						className="zee-curve-editor-button"
						style={{ marginLeft: 0, marginTop: 8 }}
						onMouseDown={() => (addDemoPoints.current = true)}
						onMouseUp={() => {
							addDemoPoints.current = false;
							closestPoint.current = null;
							handleMouseMove({ ...mousePos.current });
							allowToDraw();
						}}
					>
						Hold to add curve points
					</div>
				</div>
			</div>
			<div className="zee-curve-editor-button-container">
				<div className="zee-curve-editor-reset zee-curve-editor-button blocked-button" onClick={resetProjection}>
					Reset View
				</div>
				<div
					className="zee-curve-editor-button"
					onMouseDown={() => {
						setZoomIn(x => x + 1);
						startZoomInterval(1);
					}}
				>
					+
				</div>
				<div
					className="zee-curve-editor-button"
					onMouseDown={() => {
						setZoomOut(x => x + 1);
						startZoomInterval(-1);
					}}
				>
					−
				</div>
			</div>
		</div>
	);
};

export default CurveEditor;
