/* eslint-disable node/no-unsupported-features/es-syntax */
/*  "homepage": "/common/curves/",  */
import { DrawKeyframe, KeyframeType } from '../animationTypes';
import { Rect } from '../commonTypes';
import { EasingEditorTheme } from './types';
import { isValidCurve } from './utils';
interface ownProps {
	keyframes: DrawKeyframe[];
	projectionRect: Rect; // temporary prop
	renderRect: Rect;
	theme: EasingEditorTheme;
	isEditable: boolean;
}

const SvgCurve = (props: ownProps) => {
	const { theme, keyframes, projectionRect, renderRect, isEditable } = props;
	// draws temporary rectangle first, then curve, controls, then curve points
	return (
		<svg className="zee-svg-curves" width={renderRect.width} height={renderRect.height}>
			<>
				{projectionRect && (
					<rect
						key="base-rect"
						x={projectionRect.x}
						y={projectionRect.y}
						width={projectionRect.width}
						height={projectionRect.height}
						stroke={theme.uiColor}
						strokeWidth={theme.uiLineWidth}
						fill="transparent"
					/>
				)}
				{keyframes.map((keyframe: DrawKeyframe, idx: number, arr: DrawKeyframe[]) => {
					if (idx === arr.length - 1) {
						return null;
					}
					const { start, end, startControl, endControl } = keyframe;
					const isValid = isValidCurve(start, startControl, endControl, end);
					return (
						<path
							key={`curve-path-${idx}`}
							d={`M${start?.x},${start?.y} C${startControl?.x},${startControl?.y} ${endControl?.x},${endControl?.y} ${end?.x},${end?.y}`}
							stroke={isValid ? theme.defaultColor : theme.errorColor}
							strokeWidth={theme.defaultLineWidth}
							strokeLinecap="round"
							fill="transparent"
						></path>
					);
				})}
				{isEditable &&
					keyframes.map((drawKeyframe: DrawKeyframe, idx: number) => {
						const { start, end, startControl, endControl } = drawKeyframe;
						return (
							<g key={`control-group-${idx}`}>
								{startControl && start && (
									<>
										<line
											key="control-line-start"
											x1={start?.x}
											y1={start?.y}
											x2={startControl?.x}
											y2={startControl?.y}
											stroke={theme.controlColor}
											width={theme.controlLineWidth}
										/>
										<circle key="control-circle-start" cx={startControl?.x} cy={startControl?.y} r={theme.smallRadius} fill={theme.controlColor} />
									</>
								)}
								{endControl && end && (
									<>
										<line
											key="control-line-end"
											x1={end?.x}
											y1={end?.y}
											x2={endControl?.x}
											y2={endControl?.y}
											stroke={theme.controlColor}
											width={theme.controlLineWidth}
										/>
										<circle key="control-circle-end" cx={endControl?.x} cy={endControl?.y} r={theme.smallRadius} fill={theme.controlColor} />
									</>
								)}
							</g>
						);
					})}
				{keyframes.map((drawKeyframe: DrawKeyframe, idx: number) => {
					const { start, type } = drawKeyframe;
					return (
						<circle
							key={`curve-circle-${idx}`}
							cx={start?.x}
							cy={start?.y}
							r={theme.largeRadius}
							fill={type === KeyframeType.Relative ? theme.anchorColor : theme.defaultColor}
						/>
					);
				})}
			</>
		</svg>
	);
};

export default SvgCurve;
