export const mapNumber = (num: number, origin: EdgeNumbers, target: EdgeNumbers) =>
	((num - origin.min) / (origin.max - origin.min)) * (target.max - target.min) + target.min;

export const mapPoint = (point: Point | null, origin: Rect | null, target: Rect | null): Point | null => {
	if (!point || !origin || !target) {
		return null;
	}
	return {
		x: mapNumber(point.x, { min: origin.x, max: origin.x + origin.width }, { min: target.x, max: target.x + target.width }),
		y: mapNumber(point.y, { min: origin.y, max: origin.y + origin.height }, { min: target.y, max: target.y + target.height }),
	};
};

export const defaultCurveRect: Rect = { x: 0, y: 0, width: 1, height: 1 };

export const drawKeyframesOnCanvas = (ctx: CanvasRenderingContext2D, drawKeyframes: DrawKeyframe[], theme: EasingEditorTheme, drawControls?: boolean) => {
	drawKeyframes.forEach((drawKeyframe: DrawKeyframe) => {
		const { start, startControl, endControl, end } = drawKeyframe;
		if (start && startControl && endControl && end) {
			const isValid = isValidCurve(start, startControl, endControl, end);
			drawCurve({ ctx, drawKeyframe, color: isValid ? theme.defaultColor : theme.errorColor, lineWidth: theme.defaultLineWidth });
		}
	});
	if (drawControls) {
		drawKeyframes.forEach((drawKeyframe: DrawKeyframe) => {
			const { start, end, startControl, endControl } = drawKeyframe;
			drawLine({ ctx, start, end: startControl, color: theme.controlColor, lineWidth: theme.controlLineWidth });
			drawLine({ ctx, start: endControl, end, color: theme.controlColor, lineWidth: theme.controlLineWidth });
		});
	}
	drawKeyframes.forEach((drawKeyframe: DrawKeyframe, idx: number, arr: DrawKeyframe[]) => {
		const { start } = drawKeyframe;
		// TODO: it's just temporary coloring of the first and last keyframe, extend to static
		const isEdge = !idx || idx === arr.length - 1;
		drawCircle({ ctx, center: start, radius: theme.largeRadius, color: isEdge ? theme.defaultColor : theme.anchorColor });
	});
	if (drawControls) {
		drawKeyframes.forEach((drawKeyframe: DrawKeyframe) => {
			const { startControl, endControl } = drawKeyframe;
			drawCircle({ ctx, center: startControl, radius: theme.smallRadius, color: theme.controlColor });
			drawCircle({ ctx, center: endControl, radius: theme.smallRadius, color: theme.controlColor });
		});
	}
};

// for presentation
const presCircSize = 40;
const drawRoundRect = (ctx, x, y, w, h, r) => {
	if (w < 2 * r) r = w / 2;
	if (h < 2 * r) r = h / 2;
	ctx.beginPath();
	ctx.moveTo(x + r, y);
	ctx.arcTo(x + w, y, x + w, y + h, r);
	ctx.arcTo(x + w, y + h, x, y + h, r);
	ctx.arcTo(x, y + h, x, y, r);
	ctx.arcTo(x, y, x + w, y, r);
	ctx.fill();
};
export const drawTimeElements = (ctx: CanvasRenderingContext2D, pojectionRect: Rect, timePercent: number, projValuePercent: number, valuePercent: number) => {
	const playheadPosition = pojectionRect.width * timePercent + pojectionRect.x;
	const valuePosition = pojectionRect.height * projValuePercent + pojectionRect.y;

	drawLine({
		ctx,
		start: { x: playheadPosition, y: pojectionRect.y },
		end: { x: playheadPosition, y: pojectionRect.y + pojectionRect.height },
		color: '#FFFFFF',
		lineWidth: 1,
	});
	ctx.setLineDash([10, 10]);
	drawLine({
		ctx,
		start: { x: pojectionRect.x, y: valuePosition },
		end: { x: pojectionRect.x + pojectionRect.width, y: valuePosition },
		color: 'rgba(255, 255, 255, 0.22)',
		lineWidth: 1,
	});
	ctx.setLineDash([]);

	ctx.fillStyle = '#161616';
	const bgRect: Rect = { x: pojectionRect.x, y: pojectionRect.y + pojectionRect.height + presCircSize / 2, width: pojectionRect.width, height: presCircSize };
	drawRoundRect(ctx, bgRect.x, bgRect.y, bgRect.width, bgRect.height, presCircSize / 2);

	drawCircle({
		ctx,
		center: {
			x: bgRect.x + bgRect.height / 2 + (bgRect.width - presCircSize) * valuePercent,
			y: bgRect.y + bgRect.height / 2,
		},
		radius: bgRect.height / 2,
		color: '#7d7d7d',
	});
};
// for peresentation end

export const drawCurve = (props: { ctx: CanvasRenderingContext2D; drawKeyframe: DrawKeyframe; color: string; lineWidth: number }) => {
	const { ctx, drawKeyframe, color, lineWidth } = props;
	const { start, startControl, endControl, end } = drawKeyframe;
	if (!start || !startControl || !endControl || !end) {
		return;
	}
	ctx.strokeStyle = color;
	ctx.lineWidth = lineWidth;
	ctx.beginPath();
	ctx.moveTo(start.x, start.y);
	ctx.bezierCurveTo(startControl.x, startControl.y, endControl.x, endControl.y, end.x, end.y);
	ctx.stroke();
};

export const drawLine = (props: { ctx: CanvasRenderingContext2D; start: Point | null; end: Point | null; color: string; lineWidth: number }) => {
	const { ctx, start, end, color, lineWidth } = props;
	if (!start || !end) {
		return;
	}
	ctx.strokeStyle = color;
	ctx.lineWidth = lineWidth;
	ctx.beginPath();
	ctx.moveTo(start.x, start.y);
	ctx.lineTo(end.x, end.y);
	ctx.stroke();
};

export const drawCircle = (props: { ctx: CanvasRenderingContext2D; center: Point | null; radius: number; color: string }) => {
	const { ctx, center, radius, color } = props;
	if (!center || !radius) {
		return;
	}
	ctx.fillStyle = color;
	ctx.beginPath();
	ctx.arc(center.x, center.y, radius, 0, 2 * Math.PI);
	ctx.fill();
};

export const drawRect = (props: { ctx: CanvasRenderingContext2D; rect: Rect; color: string; lineWidth: number }) => {
	const { ctx, rect, color, lineWidth } = props;
	if (!rect) {
		return;
	}
	ctx.strokeStyle = color;
	ctx.lineWidth = lineWidth;
	ctx.beginPath();
	ctx.rect(rect.x, rect.y, rect.width, rect.height);
	ctx.stroke();
};

export const getValueByPercent = (value1, value2, percentage) => {
	// this functionality is going to be much more complicated :)
	return value1 + (value2 - value1) * percentage;
};

export const getPercentByValue = (value1, value2, value) => {
	return (value - value1) / (value2 - value1);
};

const isPointInCircle = (point: Point | null, circleCenter: Point | null, circleRadius: number): boolean => {
	if (!point || !circleCenter) {
		return false;
	}
	const pointsDistace = Math.sqrt(Math.pow(point.x - circleCenter.x, 2) + Math.pow(point.y - circleCenter.y, 2));
	if (pointsDistace < circleRadius) {
		return true;
	}
	return false;
};

export const pointUnderCursor = (point: Point, drawKeyframes: DrawKeyframe[], theme: EasingEditorTheme): ActivePoint | null => {
	// TODO: implement turned on and of controls, so invisible controsl don't return
	let idx = -1;

	while (idx < drawKeyframes.length - 1) {
		idx++;
		const drawKeyframe = drawKeyframes[idx];
		if (isPointInCircle(point, drawKeyframe.startControl, theme.smallRadius)) {
			return { idx, aspect: KeyframeAspect.StartControl };
		}
		if (isPointInCircle(point, drawKeyframe.endControl, theme.smallRadius)) {
			return { idx, aspect: KeyframeAspect.EndControl };
		}
		if (isPointInCircle(point, drawKeyframe.start, theme.largeRadius)) {
			return { idx, aspect: KeyframeAspect.Start };
		}
		// we don't search for end points so it's always the first aspect getting activated
	}

	return null;
};

export const isValidCurve = (P0: Point | null, C0: Point | null, C1: Point | null, P1: Point | null) => {
	if (!P0 || !C0 || !C1 || !P1) {
		return false;
	}
	const segmentNum = 40;
	let count = 0;
	let prevX = 0;
	while (count < segmentNum) {
		const bezierPoint = getBezierPoint((1 / segmentNum) * count, P0, C0, C1, P1);
		if (bezierPoint.x < prevX && count) {
			return false;
		}
		prevX = bezierPoint.x;
		count++;
	}
	return true;
};

const getBezierPoint = (t, C0, C1, C2, C3) => {
	return {
		x: (1 - t) ** 3 * C0.x + t * C1.x * (3 * (1 - t) ** 2) + C2.x * (3 * (1 - t) * t ** 2) + C3.x * t ** 3,
		y: (1 - t) ** 3 * C0.y + t * C1.y * (3 * (1 - t) ** 2) + C2.y * (3 * (1 - t) * t ** 2) + C3.y * t ** 3,
	};
};

export const getDistance = (pointA, pointB) => {
	const xDiff = pointA.x - pointB.x;
	const yDiff = pointA.y - pointB.y;
	return Math.sqrt(xDiff * xDiff + yDiff * yDiff);
};

export const closestPointOnCurve = (keyframes: DrawKeyframe[], point: Point): { point: Point | null; idx: number } => {
	let nearIdx = 0;
	const nearestPoint: Point | null = keyframes.reduce((acc: Point | null, drawFrame: DrawKeyframe, idx: number) => {
		if (drawFrame.start && drawFrame.startControl && drawFrame.endControl && drawFrame.end) {
			const curve = [
				new PaperPoint(drawFrame.start.x, drawFrame.start.y),
				new PaperPoint(drawFrame.startControl.x - drawFrame.start.x, drawFrame.startControl.y - drawFrame.start.y),
				new PaperPoint(drawFrame.endControl.x - drawFrame.end.x, drawFrame.endControl.y - drawFrame.end.y),
				new PaperPoint(drawFrame.end.x, drawFrame.end.y),
			];
			const bezier = new Curve(curve[0], curve[1], curve[2], curve[3]);
			const closePoint = bezier.getNearestPoint(new PaperPoint(point.x, point.y));
			if (!acc || getDistance(closePoint, point) < getDistance(acc, point)) {
				nearIdx = idx;
				return closePoint;
			}
		}
		return acc;
	}, null);
	return { point: nearestPoint, idx: nearIdx };
};

export const splitCurve = (drawFrame: DrawKeyframe, point: Point): [DrawKeyframe, DrawKeyframe] | null => {
	if (drawFrame.start && drawFrame.startControl && drawFrame.endControl && drawFrame.end) {
		const curve = [
			new PaperPoint(drawFrame.start.x, drawFrame.start.y),
			new PaperPoint(drawFrame.startControl.x - drawFrame.start.x, drawFrame.startControl.y - drawFrame.start.y),
			new PaperPoint(drawFrame.endControl.x - drawFrame.end.x, drawFrame.endControl.y - drawFrame.end.y),
			new PaperPoint(drawFrame.end.x, drawFrame.end.y),
		];

		const bezier = new Curve(curve[0], curve[1], curve[2], curve[3]);
		const location = bezier.getLocationOf(new PaperPoint(point.x, point.y));
		const divided = bezier.divideAt(location);

		if (!divided) return null;

		const newDrawFrame1: DrawKeyframe = {
			start: { x: bezier.point1.x, y: bezier.point1.y },
			startControl: { x: bezier.point1.x + bezier.handle1.x, y: bezier.point1.y + bezier.handle1.y },
			endControl: { x: bezier.point2.x + bezier.handle2.x, y: bezier.point2.y + bezier.handle2.y },
			end: { x: bezier.handle2.x, y: bezier.handle2.y },
			type: drawFrame.type,
		};

		const newDrawFrame2: DrawKeyframe = {
			start: { x: divided.point1.x, y: divided.point1.y },
			startControl: { x: divided.point1.x + divided.handle1.x, y: divided.point1.y + divided.handle1.y },
			endControl: { x: divided.point2.x + divided.handle2.x, y: divided.point2.y + divided.handle2.y },
			end: { x: divided.handle2.x, y: divided.handle2.y },
			type: KeyframeType.Relative,
		};
		return [newDrawFrame1, newDrawFrame2];
	}
	return null;
};
